import Barra from '../components/nav';
import Rodape from '../components/footer';



function Sobre() {
    return(   
     <div className="App">      
       <Barra />   
        <main>  
         <div className='center-sobre'>           
               
         <section class="center-prime">
          <div class="center-prime-texto">
           <h1 class="center-prime-texto-titulo">O QUE  HOGWARTS LEGACY?</h1>
           <h2 class="center-prime-texto-subtitulo">
            <p>Hogwarts Legacy  um jogo de ao e RPG imersivo e de mundo aberto, 
              ambientado no primeiro mundo apresentado nos livros da srie Harry Potter. </p>
            <p>Embarque em uma jornada por locais familiares e novos enquanto explora e descobre criaturas fantsticas,
               personaliza seu personagem e cria poes, domina feitios, 
               melhora talentos e se torna o bruxo que voc sempre quis ser.</p>
            <p>Viva na Hogwarts do sculo XIX. 
              Seu personagem  um estudante que possui a chave para um segredo antigo, 
              o qual ameaa destroar o mundo bruxo. Descubra a sensao de viver em Hogwarts enquanto faz aliados, 
              enfrenta bruxos das trevas e decide o destino do mundo bruxo. </p>
    
            Voc decide seu legado. Viva o que no foi escrito.</h2>
           
            
          </div>
         
         </section>

         <section class="center-corpo">
          <h3 class="center-corpo-titulo">Mais algumas informaes</h3>
          <p class="center-corpo-paragrafo">Hogwarts Legacy ser lanado no dia  
           <strong> 10 de fevereiro de 2023 </strong> 
            para PlayStation, Xbox e PC. A data de lanamento para Nintendo Switch ser revelada em breve.
          </p>
          <p class="center-corpo-paragrafo">Hogwarts Legacy estar disponvel para os consoles
           <strong> PlayStation5, PlayStation4, PlayStation4 Pro, Xbox Series X|S, Xbox One, Nintendo Switch e PC.</strong> 
            
          </p>
          <p class="center-corpo-paragrafo"> possvel vincular os dados da sua conta   
           <strong> <a href='https://www.wizardingworld.com'>Wizarding World</a>  </strong>
            ao jogo e assim aprimorar a sua histria
          </p>
         </section>
        </div>
        </main>
      <Rodape />
    </div>
    )
}

export default Sobre;